@import "layout";

.sliding-menu-button {
  padding-bottom: 6px;
}

img.center {
	display: block;
	margin: 0 auto;
}

img.logo {
	width: 162px;
}

#btn-close-modal {
  width:100%;
  text-align: center;
  cursor:pointer;
  color:#fff;
}

.close-animatedModal {
  text-align: center;
  cursor:pointer; 
  padding-top: 1em;
  padding-bottom: 1em;
}

.modal-content {
  padding: 1em;
  color: white;
  text-align: center;  
}

.link {
  text-decoration: unset;
}

.link:hover {
  text-decoration: underline;
  color: white;
}

.adr {
  color: white;
  font-size: 1em;
}

  .my-gallery {
    width: 100%;
    float: left;
  }
  .my-gallery img {
    width: 100%;
    height: auto;
  }
  .my-gallery figure {
    display: block;
    float: left;
    margin: 0 5px 5px 0;
    width: 150px;
  }
  .my-gallery figcaption {
    display: none;
  }

.pswp__img {
  width: auto !important;
}


.sliding-menu-content .menu-item a {
    padding-left: 1em;
}

.sliding-menu-content .menu-item .title {
    font-size: 2.5rem;
}

.sliding-menu-content .menu-item .teaser {
    position: absolute;
    opacity: 0.8;
}

// @media screen and (min-width: 37.5em)
.sliding-menu-content .menu-item .teaser {
    width: 150px;
}

// @media screen and (min-width: 37.5em)
.sliding-menu-content .menu-item .title, .sliding-menu-content .menu-item .title {
    margin-left: 170px;
    font-size: 32px;
    font-size: 2rem;
    line-height: 1.5;
    margin-bottom: 0;
}
